.about__container{

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about__cards{
    justify-items: center;
    display: grid;
    gap: 1.5rem;
}

p{
    text-align: center;
    color: #015994;
}

section > h2 {
    color: #015994;
    margin-bottom: 3rem;
}

.about__card{

    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: .6rem;
    text-align: center;
    transition: var(--transition);
    height: fit-content;
}

.about__card:hover{

    background: transparent;
    border-color: #015994;
    color: #015994 !important;
    cursor: default;
}

.about__card:hover h5,
.about__card:hover small {
    color:#015994;
}

.about__icon{

    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}


.about__card small{

    font-size: .7rem;
    color: var(--color-light);
}

.about__content p{

    margin: 2rem 0 2.6rem;
    color: #015994;
    width: 500px;
}

/* Media queries for medium devices(Tablets) */

@media screen and (max-width: 1024px) {

    .about__container{

        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{

        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{

        margin: 1rem 0 1.5rem;
    }


}

/* Media queries for small devices(Mobiles) */

@media screen and (max-width: 600px) {

    .about__me{

        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        
        gap: 1rem;
    }

    .about__content{

        text-align: center;
    }

    .about__content p{

        margin: 1.5rem 0;
        width: fit-content;
    }
}